body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  background-color: #8a0000;
}

.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.video-container {
  position: relative;
  display: flex;
  width: 100vw;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 50px); /* Reduce height to account for the tab menu */
  overflow: hidden;
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
img {
  margin: 0;
  padding: 20;
  width: 100%;
  height: 100%;
  object-fit: scale-down;
}
.tab-menu {
  position: fixed;
  bottom: 0;
  margin-left: 2.5%;
  width: 95%;
  height: 50px;
  background-color: #fcff2dda;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 15px;
}

.tab {
  color: rgb(87, 55, 8);
  flex: 1;
  text-align: center;
  padding: 10px 0;
  cursor: none;
  border-radius: 15px;
}

.tab.active {
  background-color: #ffc400;
}
